* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus,
:active {
  outline: none;
}
a:focus,
a:active {
  outline: none;
}
nav,
footer,
header,
aside {
  display: block;
}
html,
body {
  font-family: "Poppins";
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/************************************************************/
#root {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  flex-direction: column;
  font-size: 18px;
  font-weight: 500;
  font-family: "Space Grotesk";
}
.container {
  max-width: 1470px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  min-height: 100%;
  @media (min-width: 767px) {
    align-items: center;
    justify-content: center;
    flex-direction: row;
  }
}
main {
  flex: 1 1 100%;
}
:root {
  --red: hsl(0, 100%, 66%);
  --White: hsl(0, 0%, 100%);
  --Light-grayish-violet: hsl(270, 3%, 87%);
  --Dark-grayish-violet: hsl(279, 6%, 55%);
  --Very-dark-violet: hsl(278, 68%, 11%);
  --inear-gradient: hsl(249, 99%, 64%) to hsl(278, 94%, 30%);
}
body {
  @media (min-width: 767px) {
    background: url("./components/images/bg-main-desktop.png") no-repeat;
    background-size: calc(100% / 3) 100%;
  }
  @media (max-width: 767px) {
    background: url("./components//images/bg-main-mobile.png") no-repeat;
    background-size: 100% 30%;
  }
}
.App {
  min-height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

form {
  flex: 0 0 50%;
  width: 400px;
  height: 400px;
  @media (max-width: 767px) {
    background-color: #fff;
    flex: 1 1 100%;
    width: 100%;
  }
  display: flex;
  flex-direction: column;
  align-items: center;
  & button {
    text-align: center;
    border-radius: 5px;
    color: #fff;
    width: 80%;
    padding-top: 1rem;
    padding-bottom: 1rem;
    background-color: var(--Very-dark-violet);
  }
}
form input {
  padding: 0 10px;
  width: 80%;
  height: 50px;
  border-radius: 5px;
  border: 1px solid #000;
  outline: none;
  margin: 10px;
}
.cardCredit-front {
  top: 20%;
  margin-bottom: 10%;
  z-index: 3;

  .cardFront {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: -40px;
    @media (max-width: 767px) {
      top: 58%;
      left: -20px;
    }
  }
  .cardNumber {
    top: 65%;
    left: -5%;
    @media (max-width: 767px) {
      left: 0;
      top: 120%;
    }
  }
  display: block;

  width: 325px;
  height: 176px;

  position: relative;
  @media (min-width: 967px) {
    width: 447px;
    height: 245px;
  }
}
.cardCredit-back {
  display: block;
  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 60px;
  }
  width: 325px;
  height: 176px;
  .cardCVC {
    top: 46%;
    right: 0;
    max-width: 30px;
  }

  @media (min-width: 967px) {
    width: 447px;
    height: 245px;
  }
  position: relative;
  @media (max-width: 767px) {
    position: absolute;
    top: 0;
    right: 15%;
  }
}
.cardCredit {
  position: relative;
  flex: 1 1 50%;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  @media (min-width: 967px) {
    padding-right: 10%;
  }
  @media (max-width: 767px) {
    margin-bottom: 100px;
    flex: 1 1 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.cardNumber,
.cardCVC,
.expDate,
.fullname {
  position: absolute;
  color: #fff;
  text-transform: uppercase;
}

.fullname {
  bottom: 25px;
  left: -5%;
  @media (max-width: 767px) {
    left: 0;
    bottom: -45%;
  }
}
.expDate {
  bottom: 25px;
  right: 15%;
  @media (max-width: 767px) {
    right: 15%;
    bottom: -45%;
  }
}
.complete {
  display: flex;
  flex: 0 0 50%;
  align-items: center;
  justify-content: center;
  &-body {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .complete-icon {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
    .complete-title {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
    .complete-text {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
    .complete-button {
      text-align: center;
      border-radius: 5px;
      color: #fff;
      width: 80%;
      padding-top: 1rem;
      padding-bottom: 1rem;
      background-color: var(--Very-dark-violet);
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
  }
}

.alert {
  border: solid 1px --red;
}
.card-logo {
  position: absolute;
  z-index: 2;
  top: 5%;
  left: -5%;
  @media (max-width: 767px) {
    left: 0px;
    top: 67%;
  }
}
